html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  background: #e0e0e0;
  font-family: Open Sans, Roboto, Arial, Helvetica, sans-serif;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px; }

.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .container--hidden {
    display: none; }

.loader {
  padding: 18px;
  width: 100%;
  background: #fff; }
  .loader__item {
    border: 4px solid #f5f5f5;
    border-top: 4px solid #6a5acd;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spin 1s linear infinite;
    margin: 0 auto; }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

.form {
  width: 70%;
  display: flex;
  justify-content: center; }
  .form__box:first-child {
    margin-right: 16px; }
  .form__filter, .form__select {
    padding: 4px; }

.table {
  width: 70%;
  border-collapse: collapse;
  box-shadow: 0 0 4px 1px #5757579d;
  border-radius: 8px;
  margin-bottom: 12px; }
  .table__data, .table__header {
    text-align: center;
    padding: .75rem; }
  .table__row {
    background: #fff; }
    .table__row:nth-of-type(2n-1) {
      background: #f5f5f5; }
    .table__row:last-of-type > td:first-child {
      border-radius: 0 0 0 8px; }
    .table__row:last-of-type > td:last-child {
      border-radius: 0 0 8px 0; }
    .table__row:hover {
      cursor: pointer;
      background: rgba(106, 90, 205, 0.0196078); }
  .table__header {
    background: #6a5acd;
    color: #fff; }
    .table__header:first-child {
      border-radius: 8px 0 0 0; }
    .table__header:last-child {
      border-radius: 0 8px 0 0; }

.pagination__button {
  width: 30px;
  padding: 4px;
  margin-bottom: 4px;
  border: 1px solid #6a5acd;
  border-radius: 4px;
  background: #fff; }
  .pagination__button:hover {
    cursor: pointer;
    background: #6a5acd;
    color: #fff; }

.details {
  display: none; }
  .details--visible {
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }

.chart {
  margin-bottom: 24px;
  width: 100%; }

.return {
  width: 30px;
  padding: 4px;
  margin-bottom: 4px;
  border: 1px solid #6a5acd;
  border-radius: 4px;
  background: #fff;
  width: fit-content; }
  .return:hover {
    cursor: pointer;
    background: #6a5acd;
    color: #fff; }

.tooltip {
  display: none; }
  .tooltip--visible {
    display: block;
    padding: 8px;
    border-radius: 4px;
    background: #f5f5f5; }

@media (max-width: 768px) {
  .form {
    width: 100%;
    display: flex;
    flex-direction: column; }
  .table {
    width: 100%; } }

@media (max-width: 540px) {
  body {
    font-size: smaller; } }

@media (max-width: 424px) {
  body {
    font-size: x-small; } }

@media (max-width: 370px) {
  .form__box {
    margin: 4px; } }
